nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: #f8f8f8;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    flex-wrap: wrap;
    z-index: 1000;
}

.navlists ul {
    display: flex;
    gap: 2.5rem;
    list-style: none;
    margin: 0;
    padding: 0;
}

.navlists ul li {
    position: relative;
}

.navlists ul li a {
    text-decoration: none;
    color: #333;
    font-size: 1rem;
    font-weight: 500;
    transition: color 0.3s ease;
}

.navlists ul li a::before {
    content: "";
    position: absolute;
    width: 0%;
    height: 2px;
    bottom: -4px;
    left: 0;
    background-color: #333;
    transition: width 0.3s ease;
}

.navlists ul li a:hover::before {
    width: 100%;
}

.navlists ul li a:hover {
    color: #007BFF;
}

.searchandcart svg {
    margin-left: 1rem;
    cursor: pointer;
    transition: transform 0.3s ease;
}

.searchandcart svg:hover {
    transform: scale(1.2);
}

/* Dropdown Styling */
.dropdown {
    position: absolute;
    top: 100%;
    left: 0;
    background: #fff;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
    list-style: none;
    padding: 0;
    margin: 0;
    min-width: 150px;
    display: none;
    animation: fadeIn 0.2s ease-out;
    z-index: 1001;
}

.navlists ul li:hover .dropdown {
    display: block;
}

.dropdown li a {
    padding: 0.75rem 1.5rem;
    display: block;
    color: #333;
    white-space: nowrap;
    transition: background 0.01s ease, color 0.3s ease;
}

/* Animation Styling */
@keyframes fadeIn {
    0% { opacity: 0; transform: translateY(-10px); }
    100% { opacity: 1; transform: translateY(0); }
}

nav {
    animation: fadeIn 1s ease-out;
}

@keyframes slideIn {
    0% { opacity: 0; transform: translateX(-20px); }
    100% { opacity: 1; transform: translateX(0); }
}

.navlists ul li {
    opacity: 0;
    animation: slideIn 0.5s ease-out forwards;
}

.navlists ul li:nth-child(1) { animation-delay: 0.1s; }
.navlists ul li:nth-child(2) { animation-delay: 0.2s; }
.navlists ul li:nth-child(3) { animation-delay: 0.3s; }
.navlists ul li:nth-child(4) { animation-delay: 0.4s; }
.navlists ul li:nth-child(5) { animation-delay: 0.5s; }

@keyframes popIn {
    0% { opacity: 0; transform: scale(0.8); }
    100% { opacity: 1; transform: scale(1); }
}

.searchandcart svg {
    opacity: 0;
    animation: popIn 0.5s ease-out forwards;
}

.searchandcart svg:nth-child(1) { animation-delay: 0.6s; }
.searchandcart svg:nth-child(2) { animation-delay: 0.7s; }
