footer {
    background-color: #333;
    color: #fff;
    padding: 2rem 0;
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.6s, transform 0.6s;
}

.footer-appear {
    opacity: 1;
    transform: translateY(0);
}

.footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.footer-section {
    flex: 1;
    padding: 1rem;
}

.footer-section h3 {
    color: #fff;
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.footer-section p {
    font-size: 0.9rem;
    line-height: 1.6;
}

.contact {
    margin-top: 1rem;
}

.contact span {
    display: block;
    margin-bottom: 0.5rem;
}

.socials a {
    color: #fff;
    font-size: 1.5rem;
    margin-right: 1rem;
}

.links ul {
    list-style-type: none;
    padding: 0;
}

.links ul li {
    margin-bottom: 0.5rem;
}

.links ul li a {
    color: #fff;
    text-decoration: none;
}

.contact-form {
    max-width: 400px;
}

.text-input {
    width: 100%;
    padding: 0.5rem;
    margin-bottom: 1rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1rem;
}

.contact-btn {
    display: inline-block;
    background-color: #008CBA;
    color: #fff;
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
}

.contact-btn:hover {
    background-color: #005F6B;
}

.footer-bottom {
    text-align: center;
    padding-top: 1rem;
    background-color: #222;
    color: #ccc;
}
