/* Section Styling */
.section {
    position: relative;
    width: 100%;
    height: 500px; /* Initial height */
    background-image: url('./images/BloggerSection.png');
    background-size: cover; /* Ensures the image covers the section */
    background-position: center; /* Centers the image */
    background-repeat: no-repeat; /* Prevents the image from repeating */
    z-index: -1; /* Ensure section is below the navbar */
    opacity: 0;
    animation: fadeInSection 1.5s ease-out forwards;
}

.section::before,
.section::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    height: 100px; /* Adjust height of the gradient */
    z-index: 2; /* Ensure gradient overlays the image */
    pointer-events: none; /* Allow clicks to pass through */
    opacity: 0;
    animation: fadeInGradients 1.5s ease-out forwards;
}

.section::before {
    top: 0;
    background: linear-gradient(to bottom, rgb(94, 94, 94), transparent);
    animation-delay: 0.3s;
}

.section::after {
    bottom: 0;
    background: linear-gradient(to top, #f8f8f8, transparent);
    animation-delay: 0.3s;
}

.textonimg {
    position: absolute;
    bottom: 70px;
    left: 70px;
    color: white;
    background-color: rgba(0, 0, 0, 0.5);
    padding: 10px 20px;
    border-radius: 5px;
    z-index: 9; /* Ensure text is above the gradients */
    opacity: 0;
    animation: fadeInText 1.5s ease-out forwards;
    animation-delay: 0.6s;
}

.textonimg h3 {
    margin: 0;
    font-size: 1.5rem;
    animation: pulse 2s infinite;
}

/* Responsive Styling */
@media (max-width: 1200px) {
    .section {
        height: 400px;
    }
}

@media (max-width: 992px) {
    .section {
        height: 350px;
    }

    .textonimg h3 {
        font-size: 1.3rem;
    }
}

@media (max-width: 768px) {
    .section {
        height: 300px;
    }

    .textonimg h3 {
        font-size: 1.2rem;
    }

    .textonimg {
        bottom: 45px;
        left: 45px;
        padding: 8px 16px;
    }
}

@media (max-width: 576px) {
    .section {
        height: 250px;
    }

    .textonimg h3 {
        font-size: 1rem;
    }

    .textonimg {
        bottom: 30px;
        left: 30px;
        padding: 6px 12px;
    }
}

/* Keyframes for Animations */
@keyframes fadeInSection {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeInGradients {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeInText {
    0% {
        opacity: 0;
        transform: translateY(20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
    
}

@keyframes pulse {
    0%, 100% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
}